import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userState: "",
    city: "",
    street: "",
    email: undefined,
    validation: undefined,
    progressNow: '0%',
    currentStep: 1,
    steps: {
      step1: {
        userState: null,
        city: null,
        street: null
      },
      step2: {
        houseNum: null,
        floor: null,
        aptNum: null
      },
      step3: {
        placeType: null,
      },
      // step4: {
      //   placeType: null,
      // },
    },
  
  },
  mutations: {
    // Steps & Progress
    updateCurrentStep(state, payload) {
      state.currentStep = payload.currentStep
    },
    setProgress(state, payload) {
      state.progressNow = payload.progressNow + "%"
    },
    // Step 1
    setState(state, payload) {
      state.steps.step1.userState = payload.state
    },
    setCity(state, payload) {
      state.steps.step1.city = payload.city
    },
    setStreet(state, payload) {
      state.steps.step1.street = payload.street
    },
    
    // Step 2
    setHouseNum(state, payload) {
      state.steps.step2.houseNum = payload.houseNum
    },
    setFloor(state, payload) {
      state.steps.step2.floor = payload.floor
    },
    setAptNum(state, payload) {
      state.steps.step2.aptNum = payload.aptNum
    },

    // Step 3
    setPlaceType(state, payload) {
      state.steps.step3.placeType = payload.placeType
    }
  },
  actions: {},
  modules: {}
})